							let {fileID} = res.data;
							if(fileID){
								let artworkImgUrl = 'https://7463-tcb-skr4b0ax987338-2du4mbaca713d-1310200134.tcb.qcloud.la/' + filePath;
								this.$api.callFunction({
									name: 'updateArtworkInfo',
									method: 'POST',
									data: {
										token,
										artworkName,
										arrData: pd,
										userId,
										artworkImgUrl,
										artworkId,
										isNew
