									})
									let {isNew, artworkId} = res.data;
									if(isNew && artworkId){
										this.artworkId = artworkId;
									}
								}).catch(err => {
									console.log(err);
									this.showToast({
										title: '',
										duration: 1300
									})
								})
							}
						}).catch(err => {
							this.showToast({
								title: '',
								duration: 1300
							})
							console.log(err);
						})
					}
				}).catch(err => {
					this.showToast({
						title: '',
						duration: 1300
					})
					console.log(err);
				})
			},
			//
			handleQuit(){
				this.showWarning = true;
			},
			//
			navigateBack(){
				wx.miniProgram.navigateBack({
					delta: 1
				});
			},
			//
			openHelp(){
				this.switchSubmenu('');
				this.seeMore = false;
				this.showHelper = true;
			},
			//
			zoomIn(){
				let nk = this.calcZoomK(true);
				this.handleZoom(nk);
			},
			zoomOut(){
				let nk = this.calcZoomK(false);
				this.handleZoom(nk);
			},
			/**
			 * 
			 * @param {number} k 
			 */
			handleZoom(k){
				//truefalse
				let {ptX, ptY, zoomData, paintData} = this;
				if(k == zoomData.k){
					//
					return false;
				}
				if(!k || k <= 1 || typeof(k) != 'number'){
					k = 1;
					zoomData.status = false;
					zoomData.k = 1;
					zoomData.n = null;
					zoomData.zX = 0;
					zoomData.zY = 0;
					zoomData.data = [];
					this.clearAll();
					this.redraw();
				}else{
					//pointer
					var r = this.resolution;//
					var zR = Math.ceil(r / (k*2));
					var nR = zR << 1;
					var zX = ptX - zR;
					var zY = ptY - zR;
					var zrX = ptX + zR - 1;
					var zrY = ptY + zR - 1;
					var l = paintData.length;
					var od = paintData[l-1];
					var d = [];
					//
					if(zX < 0){
						zX = 0;
						zrX = nR - 1;
					}
					if(zY < 0){
						zY = 0;
						zrY = nR - 1;
					}
					if(zrX >= r){
						zrX = r - 1;
						zX = r - nR;
					}
					if(zrY >= r){
						zrY = r - 1;
						zY = r - nR;
					}
					//
					for(let i = zX;i <= zrX;i++){
						var raw = [];
						for(let j = zY;j <= zrY;j++){
							raw.push(od[i][j]);
						}
						d.push(raw);
					}
					zoomData.status = true;
					zoomData.k = k;
					zoomData.n = nR;
					zoomData.zX = zX;
					zoomData.zY = zY;
					zoomData.data = d;
					this.clearAll();
					this.redraw(d);
				}
				//this.updateSelector(this.selector.rect);
				return true;
			},
			/**
			 * 
			 * @param {Object} opt 
			 */
			updateZoomData(opt){
				let zX, zY, data;
				let l = this.paintData.length;
				let zoomData = this.zoomData;
				if(opt){
					zX = opt.zX == undefined ? zoomData.zX : opt.zX;
					zY = opt.zY == undefined ? zoomData.zY : opt.zY;
					data = opt.data || this.paintData[l-1];
				}else{
					zX = zoomData.zX;
					zY = zoomData.zY;
					data = this.paintData[l-1];
				}
				zoomData.zX = zX;
				zoomData.zY = zY;
				var r = zoomData.n;
				//
				var d = [];
				for(let i = zX;i < r + zX;i++){
					var raw = [];
					for(let j = zY;j < r + zY;j++){
						raw.push(data[i][j]);
					}
					d.push(raw);
				}
				zoomData.data = d;
			},
			/**
			 * ()
			 * @param {Boolean} u
			 */
			calcZoomK(u){
				let zoomData = this.zoomData;
				let zoom = this.zoom;
				let k = zoomData.k;
				let r = this.resolution;
				let nk;
				let l = zoom.length;
