					for(let i = 0;i < l;i++){
						if(zoom[i] > k){
							nk = zoom[i];
							break;
						}else{
							nk = k;
						}
					}
					if(r <= 16 && nk > 2){
						nk = 2;
					}else if(r > 16 && r <= 48 && nk > 3){
						nk = 3;
					}else if(r > 48 && r <= 64 && nk > 4){
						nk = 4;
					}
				}else{
					for(let i = l - 1;i >= 0;i--){
						if(zoom[i] < k){
							nk = zoom[i];
							break;
						}else{
							nk = k;
						}
					}
				}
				return nk;
			},
			getMaxKPos(){
				let r = this.resolution;
				let nk;
				if(r <= 16){
					nk = 3;
				}else if(r > 16 && r <= 48){
					nk = 4;
				}else if(r > 48 && r <= 64){
					nk = 5;
				}else{
					nk = 6;
				}
				this.MaxKPos = nk;
				return nk;
			},
