									<path d="M-78 70.0439C-4.77214 75.6937 63.873 38.9186 89.042 19.8248" stroke="#000" stroke-opacity="0.6" stroke-width="27.8" stroke-linecap="square"/>
								</svg>
							</div>
						</div>
						<div class="vector-container-box2">
							<div class="vector-container2" :style="{'width':pv2+'%'}">
								<svg class="vector-track2" width="174" height="93" viewBox="0 0 174 93" fill="none" xmlns="http://www.w3.org/2000/svg">
									<path d="M8.5 84.5C81.7 78.5 143.667 31.3333 165.5 8.5" stroke="#333333" stroke-width="16" stroke-linecap="round"/>
								</svg>
							</div>
						</div>
						<div class="tap-tap">TAP TAP</div>
						<div class="fake-pointer" :style="{'left':ptLeft+'vw','top':ptTop+'vw'}"></div>
						<img class="big-hand" src="../../static/images/help/pointy.png" alt="" :style="{'left':handLeft1+'vw','top':handTop1+'vw'}"/>
					</div>
					<div class="help-description">
						<div class="large-raw">
							<div class="line-track-box">
								<div class="line-track" style="width: 100%"></div>
							</div>
							<div class="large-text"></div>
						</div>
						<div class="large-raw">							
							<div class="line-track-box">
								<div class="line-track" style="width: 3.2vw"></div>
								<div class="line-track" style="width: 26.4vw; margin-left: 2.39vw"></div>
							</div>
							<div class="large-text"></div>
						</div>
					</div>
				</div>
				<!-- page 2 -->
				<div class="help-content">
					<div class="help-window" :style="{'height': helpWindowHeight+'vw'}">
						<div class="frame-pointer-group">
							<div class="frame-pointer-group-box">
								<div class="frame-group-text"></div>
								<div class="frame-group-text" style="left: 40vw"></div>
								<svg class="frame-svg-arrow" width="16" height="38" viewBox="0 0 16 38" fill="none" xmlns="http://www.w3.org/2000/svg" style="left: 5vw">
									<path d="M8.70711 0.292893C8.31658 -0.0976311 7.68342 -0.0976311 7.29289 0.292893L0.928932 6.65685C0.538408 7.04738 0.538408 7.68054 0.928932 8.07107C1.31946 8.46159 1.95262 8.46159 2.34315 8.07107L8 2.41421L13.6569 8.07107C14.0474 8.46159 14.6805 8.46159 15.0711 8.07107C15.4616 7.68054 15.4616 7.04738 15.0711 6.65685L8.70711 0.292893ZM7 37C7 37.5523 7.44771 38 8 38C8.55228 38 9 37.5523 9 37L7 37ZM7 1L7 37L9 37L9 1L7 1Z" fill="black"/>
								</svg>
								<svg class="frame-svg-arrow" width="16" height="38" viewBox="0 0 16 38" fill="none" xmlns="http://www.w3.org/2000/svg" style="left: 45vw">
									<path d="M8.70711 0.292893C8.31658 -0.0976311 7.68342 -0.0976311 7.29289 0.292893L0.928932 6.65685C0.538408 7.04738 0.538408 7.68054 0.928932 8.07107C1.31946 8.46159 1.95262 8.46159 2.34315 8.07107L8 2.41421L13.6569 8.07107C14.0474 8.46159 14.6805 8.46159 15.0711 8.07107C15.4616 7.68054 15.4616 7.04738 15.0711 6.65685L8.70711 0.292893ZM7 37C7 37.5523 7.44771 38 8 38C8.55228 38 9 37.5523 9 37L7 37ZM7 1L7 37L9 37L9 1L7 1Z" fill="black"/>
								</svg>
							</div>
						</div>
						<div class="frame-holder">
							<div class="frame-example-img-box">
								<div class="frame-btn" style="width: 10vw" @click="switchFrametabToPlatter"></div>
								<div class="frame-btn" style="width: 67.5vw; right: 0" @click="switchFrametabToCard"></div>
								<img class="frame-example-img" src="../../static/images/help/frame17.png" alt="" />
							</div>
						</div>
						<div class="frame-details" :style="{'width':showPlatter?'80vw':'64.8vw','right':showPlatter?'3.46667vw':'5.333333vw'}">
							<div :style="{'height':showPlatter?'35.46667vw':'0'}" class="frame-details-item">
								<img src="../../static/images/help/platter.png" alt="" class="frame-details-item"/>
							</div>
							<div :style="{'height':showColorCard?'25.6vw':'0'}" class="frame-details-item">
								<img src="../../static/images/help/colorCard.png" alt="" class="frame-details-item"/>
							</div>
						</div>
					</div>
					<div class="help-description">
						<div class="help-description-box">
							<div style="width: 100%">
								<p class="small-raw"></p>
								<p class="small-raw"></p>
							</div>
						</div>
					</div>
				</div>
				<!-- page 3 -->
				<div class="help-content">
					<div class="help-window">
						<img class="big-hand-trip " src="../../static/images/help/pointy.png" alt="" />
						<div class="help-scaler-box">
							<img src="../../static/images/help/scaler.png" alt="" />
						</div>
						<img src="../../static/images/help/grid.png" alt="" class="help-grid" />
					</div>
					<div class="help-description">
						<div class="help-description-box">
							<div style="width: 100%">
								<p class="small-raw"></p>
								<p class="small-raw"></p>
								<p class="small-raw"></p>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div class="page-progress">
				<div class="little-circle-box">
					<div class="little-circle" :style="{'background':currentPage==1?'#fff':'#000'}"></div>
					<div class="little-circle" :style="{'background':currentPage==2?'#fff':'#000'}"></div>
					<div class="little-circle" :style="{'background':currentPage==3?'#fff':'#000'}"></div>
				</div>
			</div>
		</div>
	</pix-mask>
</template>

<script>
	import pixMask from '../containers/pixcellMask.vue'
	export default {
		components:{
			pixMask
		},
		data(){
			return {
                pv1: 0,
				pv2: 0,
				handLeft1: 5,
				handTop1: 40.4,
				ptLeft: -5.334,
				ptTop: 26,
				currentPage: 1,
				oX: 0,
				didTurnPage: false,
				showPlatter: false,
				showColorCard: false,
				helpWindowHeight: 35.2
			}
		},
		watch:{
			currentPage(p){
				if(p==1){
					this.doAnimate1();
				}else{
					this.pv1 = 0;
					this.pv2 = 0;
					this.handLeft1 = 5;
					this.handTop1 = 40.4;
					this.ptLeft = -5.334;
					this.ptTop = 26;
				}
			}
		},
		mounted(){
			let intervalId = setInterval(() => {
				this.doAnimate1();
				clearInterval(intervalId);
			},50)
		},
		methods:{
			//
			doAnimate1(){
				this.pv1 = 100;
				this.pv2 = 100;
				this.handLeft1 = 41.8;
				this.handTop1 = 20.53;
				this.ptLeft = 23.46667;
				this.ptTop = 10;
			},
			//
			helperTouchstart(et){
				let tX = et.touches[0].clientX;
				this.oX = tX;
				return false;
			},
			//
			helperTouchmove(em){
				if(!this.didTurnPage){
					let mX = em.touches[0].clientX;
					let {oX} = this;
					let dX = mX-oX;
					if(dX >= 128){
						//
						this.didTurnPage = true;
						if(this.currentPage >= 2){
							this.currentPage--;
						}else{
							//
							this.currentPage -= 0.15;
							let timeoutId = setTimeout(() => {
								this.currentPage = 1;
								clearTimeout(timeoutId);
							}, 320);
						}
					}else if(dX <= -128){
						//
						this.didTurnPage = true;
						if(this.currentPage <= 2){
							this.currentPage++;
						}else{
							//
							this.currentPage += 0.15;
							let timeoutId = setTimeout(() => {
								this.currentPage = 3;
								clearTimeout(timeoutId);
							}, 320);
						}
					}
				}
				return false;
			},
			//
			helperTouchend(){
				this.didTurnPage = false;
				this.oX = 0;
			},
			switchFrametabToPlatter(){
				this.showPlatter = true;
				this.showColorCard = false;
				this.helpWindowHeight = 66.13333;
				return false;
			},
			switchFrametabToCard(){
				this.showPlatter = false;
				this.showColorCard = true;
				this.helpWindowHeight = 59.73333;
				return false;
			},
			//
			quithelp(){
				this.$emit('cancel');
				return false;
			}
		}
	}
</script>

